<template>
	<div class="leaderboard">
		<userbar></userbar>
		<div class="relative">
			<router-link to="dashboard" class="absolute br_circle bg_warning t_2 l_2 thumbnail c_white p_3">
				<i class="fas fa-arrow-left"></i>
			</router-link>
			<h1 class="c_white text-center font_2 br_white-5 br_dotted br_0 br-b_1 p-t_4">LEADERBOARD</h1>
			<div class="counter_reset">
				<playerlisting v-for="(player, index) in playersByScore" v-bind:key="index" v-bind="player"></playerlisting>
			</div>
		</div>
	</div>
</template>

<script>
import userbar from "@/components/userBar.vue";

import playerlisting from "@/components/leaderbaordperson.vue";

export default {
	components: {
		userbar,
		playerlisting
	},
	data() {
		return {
			players: [
				{ name: "frankie", score: "100121" },
				{ name: "josh", score: "3" },
				{ name: "mike", score: "4654" },
				{ name: "josh", score: "3132" },
				{ name: "simon", score: "30514" }
			]
		};
	},
	computed: {
		playersByScore: function() {
			return this.players.sort(this.compare("score", "asc"));
		}
	},
	methods: {
		compare: function(key, order = "asc") {
			return function(a, b) {
				if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
					// property doesn't exist on either object
					return 0;
				}
				const varA = typeof a[key] === "string" ? a[key].toUpperCase() : a[key];
				const varB = typeof b[key] === "string" ? b[key].toUpperCase() : b[key];
				let comparison = 0;
				if (varA > varB) {
					comparison = 1;
				} else if (varA < varB) {
					comparison = -1;
				}
				return order == "desc" ? comparison * -1 : comparison;
			};
		}
	}
};
</script>

